<template>
    <vp-dialog :title="title" v-if="isShow" ref="dialog">
        <div style="margin-bottom: 20px;">ID&nbsp;{{ deleteId }}&nbsp;</div>
        <input type="text" style="width: 200px;" v-model="userInputText"></input>
        <div style="color:red;padding-top:10px;margin-bottom: 20px;">{{tips}}  </div>
        <div class="modal-footer" slot="modal-footer">
            <button type="button" class="btn btn-primary" @click="deletehandler"></button>
            <button type="button" class="btn btn-default" data-dismiss="modal" @click="hide"></button>
        </div>
    </vp-dialog>
</template>
<script>
    import dialog  from './dialog.vue';
    export default {
        data(){
            return {
                isShow: true,
                deleteId: '',
                validateInputText: '',
                userInputText: '',
            }
        },
        components: {
            vpDialog: dialog
        },
        props : {
            title: {
                type: String,
                default: ''
            },
            deletehandler: {
                type: Function,
                required: true
            },
            tips: {
                type: String,
                default: ''
            }
        },
        methods: {
            setData(obj){
                this.deleteId = obj.id
            },
            getUserInput(){
                return this.userInputText;
            },
            show(){
                this.userInputText = '';
                this.$refs.dialog.show();
            },
            hide(){
                this.$refs.dialog.hide();
            }
        }
    }
</script>


