<template>
    <div class="top-counter-wrap">
        <counter icon="gear-outline" :count="countData.server_amount" class="server-count"></counter>
        <counter icon="grid-view" :count="countData.view_amount" class="view-count">VIEW</counter>
        <counter icon="cart-outline" :count="countData.zone_amount" class="zone-count">ZONE</counter>
        <counter icon="recording" :count="countData.record_amount" class="record-count"></counter>

    </div>
</template>
<script>
    import counter from './counter.vue';
    import Ajax from 'ajax';
    let counterAjax = new Ajax();
    import dnsData from '../../dnsData';
    let dnsServerDataUrl = dnsData('url');
    export default {
        data(){
            return {
                countData: {
                    record_amount: '',
                    server_amount: '',
                    view_amount: '',
                    zone_amount: ''
                }
                
            }       
        },
        mounted() {
            let self = this;
            counterAjax.get({
                url: dnsServerDataUrl.resourceAmount,
                success(response){
                    self.countData = response.data;
                }
            });
        },
        components:{
            counter: counter
        }
    }
</script>



