import axios from 'axios';
import {Toast, Alert} from 'vpui';
import 'vue-cookies';

let instance = axios.create({
    baseURL: '//hfdns-test.ipo.com/api/',
    // baseURL: '/api/',
    timeout: 10000,
    withCredentials: true
});

instance.interceptors.request.use(
    config => {
        let token = window.$cookies.get('dns-cookie-token');
        if(token) {
            config.headers.Authorization = `${token}`;
        }
        return config;
    },
    err => {
        console.log('reuest error');
    }
);
// http response 
instance.interceptors.response.use(
    response => {
        switch (response.data.code) {
        case 100000 :
            return response.data;
        case 100001 :
            Alert(response.data.msg, {
                buttons : {
                    '' : {
                        type: 'main',
                        click(){
                            window.$cookies.remove('dns-cookie-token');
                            window.location.href = '/';
                            this.destroy();
                        }
                    }
                }
            });
            break;
        case 105000 :
            Toast.error(response.data.msg);
            break;
        case 100002 :
            var loginEvent = document.createEvent('HTMLEvents');
            loginEvent.initEvent('GLOBAL-EVENT-LOGIN', false, false);
            loginEvent.errorMsg = response.data.msg;
            document.getElementById('J-app-div').dispatchEvent(loginEvent);
            Toast.destroy();
            break;
        default:
            break;
        }
    },
    error => {
        if (error.response) {
            let msg = '';
            switch (error.response.status) {
            case 401:
                Toast.error('');
                break;
            case 400:
                msg = error.response.data.message;

                if(typeof msg == 'object'){
                    let errorMsg = JSON.stringify(msg);
                    Alert(errorMsg);
                } else if(typeof msg == 'string'){
                    let errorMsg = msg;
                    let errorMsgDetail = error.response.data.error;
                    if(errorMsgDetail){
                        errorMsg += ": " +errorMsgDetail;
                    }
                    Alert(errorMsg);
                }
                break;
            case 403:
                msg = error.response.data.message;
                var code = error.response.data.code;
                if(code == 40301){
                    window.$cookies.set('dns-cookie-token', '');
                    window.$cookies.set('dns-cookie-username', '');
                    setTimeout(function(){
                        window.location.href = '/';
                    }, 1000);
                }
                Toast.error(msg);
                break;
            default:
                break;
            }
            

        }
        
    }
);



function ajax() {
    let authToken =  window.$cookies.get('dns-cookie-token');
    let config = {
        header: {
            'authorization': authToken
        }
    };
    this.post = function(options){
        this.showLoading(options);
        let self = this;
        instance.post(options.url,options.data, config)
            .then((response) => {
                self.handleSuccess(response,options);
            })
    }

    this.get = function (options) {
        this.showLoading(options);
        let self = this;
        instance.get(options.url, {params:options.data}, config)
            .then((response) => {
                self.handleSuccess(response,options);
            })
    }

    this.put = (options) => {
        this.showLoading(options);
        let self = this;
        instance.put(options.url,options.data, config)
            .then((response) => {
                self.handleSuccess(response,options);
            })
    }


    this.delete = (options) => {
        this.showLoading(options);
        let self = this;
        instance.delete(options.url,options.data, config)
            .then((response) => {
                self.handleSuccess(response,options);
            })
    }

    this.showLoading = (options) => {
        if(typeof options.showLoading == 'undefined' || options.showLoading){
            Toast.loading(options.loadingMsg || '...', false, 3000);
        }
    }

    this.handleSuccess = (response, options) => {
        if(typeof response != 'undefined'){
            Toast.destroy();
            options.success(response);
        }
    }
}

export default ajax;



