<template>
    <div class="userConfig">
        <div class="inputData" v-for="(val, key, index) in usersMessage">
            <label v-if="key != ''">{{key}}</label>
            <input type="text" v-model="val.value" v-if="key != ''" :disabled="val.disabled">
        </div>
        <btn size="small" @click="saveData"></btn>
    </div>
</template>
<script>
import Ajax from 'ajax';
import { Button, Alert, Toast } from 'vpui';
import _ from '../components/fn/tool';
import dnsData from './dnsData';


var dnsAjax = new Ajax();
var userConfigUrl = dnsData('url');
var kv = {
    id : 'id',
    "username": "",
    "roles": '',
    "role": '',
    "last_seen": "",
    "position": '',
    "location": '',
    "chinese_name": '',
    "email": '',
    "member_since": "",
    "cellphone": '',
}

export default {
    data () {
        return {
            // 
            usersMessage : '',
        };
    },
    components:{
        btn: Button,
        Alert : Alert,
    },
    mounted(){
        sInit(this);
    },
    methods : {
        // 
        saveData () {
            userSave(this);
        }
    }
}


/*
    
*/
// 
var sInit = function (that) {
    getUserMessage(that);
};

// ajax
// 
var getUserMessage = function (that) {
    let userId = that.$cookies.get('dns-cookie-userid');
    dnsAjax.get({
        url : userConfigUrl.user + '/'+userId,
        success (response) {
            var user = response.data;
            user.role = '';
            user.roles.forEach((item, index, arr) => {
                user.role += item.name + ', ';
            });
            that.usersMessage = _.paraKey(_.wrap(user, {
                id : {
                    disabled : 'disabled'
                },
                'last_seen' : {
                    disabled : 'disabled'
                },
                'member_since' : {
                    disabled : 'disabled'
                },
                role : {
                    disabled : 'disabled'
                },
                username : {
                    disabled : 'disabled'
                }
            }), _.invert(kv));
        }
    });
};
// 
var userSave = function (that) {
    dnsAjax.put({
        url : userConfigUrl.user + '/' + that.usersMessage.id.value,
        data : _.wrapBack(_.paraKey(that.usersMessage, kv)),
        success (response) {
            Toast.success('');
            getUserMessage(that);
        }
    });
};


</script>
<style scoped lang="less">
    .userConfig{
        background-color:#fff;
        padding:20px;
    }
    .inputData{
        margin-bottom:10px;
    }
    label{
        color:#333;
        width:150px;
    }
    input{
        width:200px;
    }
</style>


