<template>
    <div class="line-textarea-wrap">
        <textarea class="line-number" :rows="rows" disabled="disabled" ref="txl">{{lineContent}}</textarea>
        <div class="line-content">
            <slot ref="textContentArea"></slot>
        </div>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                lineContent: '',
                textContent: ''
            }
        },
        props: {
            rows: {
                type: Number,
                default: 5
            }
        },
        methods: {
            setLine(text){
                if (!text) {
                    this.lineContent = 1;
                    return;
                } 
                var line = 1;
                var str = '1';
                text.forEach((item, index, arr) => {
                    str += '\n' + ++line;
                });
                this.lineContent = str;
            },
            setLinePosition (position) {
                this.$refs.txl.scrollTop = position;
            }
        }
    }
</script>
<style scoped>
    .line-textarea-wrap{
        position: relative;
        height: 110px;
    }
    .line-content{
        position: absolute;
        top: 0px;
        left: 28px;
        bottom: 0px;
        right: 0px;

        background: #000;
        color: #fff;
        overflow: auto;
        border-right: 0;
        line-height: 20px;
        margin: 0px;
        padding: 0px;
        padding-left: 2px;
        text-align: left;
        border: 0;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }
    .line-content textarea{
        position: absolute;
        top: 0px;
        bottom: 0px;
        right:0px;
        left:0px;
        border: 0;
        background: #000;
        color: #fff;
        line-height: 20px;
        padding: 2px;
        border: 1px #000 solid;
        width: 100%;
    }
    .line-number{
        position: absolute;
        top: 0px;
        bottom: 0px;
        border: 1px #4b4040 solid;
        background: #4b4040;
        color: #fff;
        overflow: hidden;
        width: 30px;
        border-right: 0;
        line-height: 20px;
        margin: 0px;
        padding: 0px;
        padding-left: 2px;
        text-align: center;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;

    }

</style>
<style>
    .line-content .lg-error-msg{
        position: absolute;
        top: 5px;
        right: 28px;
        text-align: right;
    }
</style>


