<template>
    <div id='admin' class="admin-block">
        <h1 style="background-color:LightGreen;"></h1>
        <div class="grid-search-form">
            <input 
                class="search-item"
                type="text" 
                :placeholder="value.name" 
                v-model="searchRoleData[key].value" 
                v-for="(value, key, index) in searchRoleData">
            </input>
            <btn @click="searchRole"></btn>
            <btn @click="resetRoleForm"></btn>
            <btn @click="addRole"></btn>
        </div>
        <vp-grid :head="tableRole.col" :data="tableRoleData" :colspan="4">
            <div :slot="'cell:option_'+i" v-for="(item,i) in tableRoleData">
                <btn size="small" @click="roleEdit(i)"></btn>
                <btn size="small" type="danger" @click="delRole(item.id)"></btn>
            </div>
        </vp-grid>
        <vp-mnpager :show-short-cut="true" ref="rolePagination" v-on:to="rolePageTo" style="float: left"></vp-mnpager>
        <!--  -->
        <vp-dialog ref="addDialogRole" title="" @dialog:save="saveRole">
            <div class="modal-body clearfix">
                <div class="form-group" style="width: 48%;float:left;margin-right:10px;">
                    <label></label>
                    <input type="text" class="form-control" v-model="editRoleVal.name" group="addRole" v-valid="{required:true}">
                </div>
                <div class="form-group" style="width: 50%;float:left">
                    <label></label>
                    <textarea cols="30" rows="10" class="form-control" v-model="editRoleVal.privilegeName" group="addRole" v-valid="{required:true}" disabled></textarea>
                </div>
                <div class="grid-search-form">
                    <input 
                        class="search-item"
                        type="text" 
                        :placeholder="value.name" 
                        v-model="searchPrivilegeData[key].value" 
                        v-for="(value, key, index) in searchPrivilegeData" 
                        :test="value['type']" v-if="value['type']=='text'"
                        :style="'width:'+ ((value.name == '') ? '62px; width:300px;' : '100px;')"
                    >
                    </input>
                    <btn @click="searchPrivilege"></btn>
                    <btn @click="resetPrivilegeForm"></btn>
                </div>
                <vp-grid :head="tablePrivilege.col" :data="tablePrivilegeData" :colspan="3">
                    <div :slot="'cell:option_'+i" v-for="(item,i) in tablePrivilegeData">
                        <btn :type="item.isDanger" size="small" @click="relation(item.id, item)">
                            <template v-if="item.isDanger"></template>
                            <template v-else></template>
                        </btn>
                    </div>
                </vp-grid>
                <vp-mnpager :show-short-cut="true" ref="privilegePagination2" v-on:to="privilegePageTo" style="float: left"></vp-mnpager>
            </div>
        </vp-dialog>

        <!--  -->
        <vp-dialog ref="editDialogRole" title="" @dialog:save="saveRole">
            <div class="clearfix">
                <div class="clearfix">
                    <div class="form-group" style="width: 48%;float:left;margin-right:10px;">
                        <label></label>
                        <input type="text" class="form-control" v-model="editRoleVal.name" group="editRole" v-valid="{required:true}">
                    </div>
                    <div class="form-group" style="width: 48%;float:left;margin-right:10px;">
                        <label></label>
                        <textarea cols="30" rows="3" disabled class="form-control" v-model="editRoleVal.privilegeName" group="editRole" v-valid="{required:true}"></textarea>
                    </div>
                </div>
                <div class="grid-search-form">
                    <input 
                        class="search-item"
                        type="text" 
                        :placeholder="value.name" 
                        v-model="searchPrivilegeData[key].value" 
                        v-for="(value, key, index) in searchPrivilegeData" 
                        :test="value['type']" v-if="value['type']=='text'"
                        :style="'width:'+ ((value.name == '') ? '62px; width:300px;' : '100px;')"
                    >
                    </input>
                    <btn @click="searchPrivilege"></btn>
                    <btn @click="resetPrivilegeForm"></btn>
                </div>
                <vp-grid :head="tablePrivilege.col" :data="tablePrivilegeData" :colspan="3">
                    <div :slot="'cell:option_'+i" v-for="(item,i) in tablePrivilegeData">
                        <btn :type="item.isDanger" size="small" @click="relation(item.id, item)">
                            <template v-if="item.isDanger"></template>
                            <template v-else></template>
                        </btn>
                    </div>
                </vp-grid>
                <vp-mnpager :show-short-cut="true" ref="privilegePagination" v-on:to="privilegePageTo" style="float: left"></vp-mnpager>
            </div>
        </vp-dialog>
    </div>
</template>
<script>
import dialog from '../_complex/dialog.vue';
import './admin.css';
import {Datagrid, Pager, Select, Button, Toast, Valid, Alert, MiniPager} from 'vpui';
import { searchData,relation,pageNub } from './methods';
import Ajax from 'ajax';
import _ from '../../components/fn/tool';
import dnsData from '../dnsData';

var dnsAjax = new Ajax();
var roleManage = dnsData('roleManage');
var roleManageUrl = dnsData('url');

export default {
    data () {
        return {
            addOrEdit : '',
            // 
            tableRole: roleManage.tableRole,
            // -
            searchRoleData : sReset(roleManage.searchRoleDataEmpty),
            // 
            tableRoleData : [],
            // 
            editRoleVal : sReset(roleManage.editRoleVal),
            // 
            tablePrivilegeData : [],
            // 
            tablePrivilege: roleManage.tablePrivilege,
            // 
            searchPrivilegeData : sReset(roleManage.searchPrivilegeDataEmpty),
        }
    },
    components:{
        vpSelect : Select,
        vpGrid: Datagrid,
        vpDialog: dialog,
        vpPager: Pager,
        btn: Button,
        Alert : Alert,
        vpMnpager: MiniPager,
    },
    mounted(){
        pageInit(this);
    },
    directives: {
        "valid":Valid,
    },
    methods: {
        // 
        resetRoleForm(){
            this.searchRoleData = sReset(roleManage.searchRoleDataEmpty);
        },
        // 
        resetPrivilegeForm(){
            this.searchPrivilegeData = sReset(roleManage.searchPrivilegeDataEmpty);
        },
        // 
        delRole (id) {
            delNoice(this, id);
        },
        // 
        addRole () {
            addRole(this);
        },
        // 
        roleEdit (index) {
            editRole(this, {
                editLogic : [index]
            });
        },
        // 
        saveRole () {
                
        },
        // 
        saveRole () {
            if (isAddOrEdit(this.addOrEdit)) {
                if (!valid(this, 'editRole')) return;
                editSave(this);
            } else {
                if (!valid(this, 'addRole')) return;
                addSave(this);
            }
                
        },
        // 
        rolePageTo (index){
            getRoleTableList(this, {
                currentPage : index
            });
        },
        // 
        privilegePageTo (index) {
            getPrivilegeTableList(this, {
                pageSize : 5,
                currentPage : index
            });
        },
        // 
        searchRole () {
            getRoleTableList(this, req(this.searchRoleData));
        },
        // 
        searchPrivilege () {
            getPrivilegeTableList(this, req(this.searchPrivilegeData))
        },
        // 
        relation (id, item) {
            relationOperation(this, id, item);
        },
    }
}


/*
    
*/
// 
/*
this.addOrEdit----
0----
1----
*/
var addRole = _.decorate(function isAddActive () {
    this.addOrEdit = 0;
});
var editRole = _.decorate(function isEditActive () {
    this.addOrEdit = 1;
});

// 
var relationPrivilege = _.decorate(function isRelationActive () {
    // 
    if (!this.editRoleVal.privilege) return;
    var arr = JSON.parse('[' + this.editRoleVal.privilege + ']');
    for (let key in this.tablePrivilegeData) {
        arr.indexOf(this.tablePrivilegeData[key].id) != -1 ? this.tablePrivilegeData[key].isDanger = 'danger' : this.tablePrivilegeData[key].isDanger = '';
    }
}, false);

// 
var pageInit = function (self) {
    getRoleTableList(self);
    // role
    addRole.add(function addLogic() {
        this.$vform['addRole'].resetStyle();
        this.$refs.addDialogRole.show();
        this.editRoleVal = sReset(roleManage.editRoleVal);
        getPrivilegeTableList(this,{
            pageSize : 5,
        });
    });
    // role
    editRole.add(function editLogic (index) {
        this.$vform['editRole'].resetStyle();
        this.$refs.editDialogRole.show();
        this.editRoleVal = _.clone(this.tableRoleData[index]);
        getPrivilegeTableList(this, {
            pageSize : 5,
        });
    });
    // roletable
    relationPrivilege.add(function relationLogic (response) {
        if (!response.data.privileges) return;
        self.tablePrivilegeData = response.data.privileges;
        _.para(self.tablePrivilegeData, {
            operation : self.searchPrivilegeData.operation.display,
            'resource_type' : self.searchPrivilegeData.resource_type.display
        });
        // isDanger
        response.data.privileges.forEach(function (item) {
            item.isDanger = '';
        });
        var total = pageNub(response.data.total);
        self.addOrEdit ? self.$refs.privilegePagination.setPage(total, response.data.current_page) : self.$refs.privilegePagination2.setPage(total, response.data.current_page)
    });
};

// 
var sReset = function (data) {
    return JSON.parse(JSON.stringify(data));
};

// 
var isAddOrEdit = _.val;

// ajax
// 
var getRoleTableList = function (self, data) {
    var obj = {
        pageSize : 10,
        currentPage : 1,
    };
    data ? Object.assign(obj, data) : obj;
    dnsAjax.get({
        url : roleManageUrl.role,
        data : data,
        success : function (response) {
            if (!response.data.roles) return;
            self.tableRoleData = addAttr(self, response.data.roles);
            self.$refs.rolePagination.setPage(pageNub(response.data.total), response.data.current_page);
        }
    });
};
// 
var getPrivilegeTableList = function (self, data) {
    var obj = {
        pageSize : 10,
        currentPage : 1,
    };
    data ? Object.assign(obj, data) : obj;
    dnsAjax.get({
        url : roleManageUrl.privilege,
        data : data,
        success : function (response) {
            relationPrivilege(self, {
                relationLogic : [response],
            });
        }
    });
};
// 
var addSave = function (self, data) {
    self.editRoleVal.privilege_ids = self.editRoleVal.privilege.split(',');
    _.trim(data);
    dnsAjax.post({
        url : roleManageUrl.role,
        data : self.editRoleVal,
        success : function () {
            Toast.success('');
            getRoleTableList(self);
            self.$refs.addDialogRole.hide();
        }
    });
};
// 
var editSave = function (self, data) {
    self.editRoleVal.privilege_ids = JSON.parse('[' + self.editRoleVal.privilege + ']');
    _.trim(self.editRoleVal);
    dnsAjax.put({
        url : roleManageUrl.role + '/' + self.editRoleVal.id,
        data : self.editRoleVal,
        success : function () {
            Toast.success('!');
            self.$refs.editDialogRole.hide();
            getRoleTableList(self);
        }
    });
};
// 
var relationOperation = function (self, id, item) {
    if (!self.editRoleVal.privilege) self.editRoleVal.privilege = '';
    var arr = JSON.parse('[' + self.editRoleVal.privilege + ']');
    var arrName = [];
    self.editRoleVal.privilegeName && arrName.push(self.editRoleVal.privilegeName.split(','));
    arrName = _.flatten(arrName);
    // 
    if (arr.indexOf(id) != -1) {
        var idx = arr.indexOf(id);
        arr.splice(idx, 1);
        arrName.splice(idx, 1);
        item.isDanger = '';
    } else {
        arr.push(id);
        arrName.push(item.name);
        item.isDanger = 'danger';
    }
    self.editRoleVal.privilege = arr.join(',');
    self.editRoleVal.privilegeName = strBr(self, arrName.join(','));
};
// idname
var addAttr = function (self, roles) {
    roles.forEach(function (item) {
        if (!Array.isArray(item.privileges)) return;
        // id
        var addPrivilegeId = [];
        var addPrivilegeName = [];
        item.privileges.forEach(function (a) {
            addPrivilegeId.push(a.id);
            addPrivilegeName.push(a.name);
        });
        item.privilege = addPrivilegeId.join(', ');
        item.privilegeName = strBr(self, addPrivilegeName.join(','));
    });
    return roles;
};
// 
var delNoice = function (self, id) {
    Alert.confirm('id' + id + '', function () {
        dnsAjax.delete({
            url : roleManageUrl.role + '/' + id,
            success : function () {
                getRoleTableList(self);
                Toast.success('!');
            }
        });
    });
};
// 
var strBr = function (self, data) {
    var newArr = data.split(',');
    newArr.forEach((item, index, arr) => {
        if (!index) return;
        if (/\n/.test(item)) return;
        arr[index] = '\n' + item;
    });
    return newArr.join(',');
};
// 
var valid = function (self, str) {
    let errLen = self.$vform[str].checkAll().length;
    self.$vform[str].checkAll();
    return !errLen;
};
// 
var req = function (data) {
    // 
    var r = {};
    for (let key in data) r[key] = data[key].value;
    r = _.compact(r);
    r = _.trim(r);
    return r;
};

</script>


